import React, { useState, useRef } from 'react';
import { HashRouter as Router, Route, Routes, useLocation } from 'react-router-dom';
import './App.css';
import ForceGraph2D from 'react-force-graph-2d';

class CypherViz extends React.Component {
  constructor({ driver }) {
    super();
    this.driver = driver;
    this.graphRef = React.createRef();
    
    this.defaultData = {
      nodes: [],
      links: []
    };
    
    this.state = {
      data: this.defaultData,
      query: `MATCH (u:User)-[r:CONNECTED_TO]->(v:User) RETURN u.name AS source, v.name AS target, u.role AS sourceRole, v.role AS targetRole, u.website AS sourceWebsite, v.website AS targetWebsite`
    };
  }

  loadData = async () => {
    let session = this.driver.session({ database: "neo4j" });
    let res = await session.run(this.state.query);
    session.close();

    let nodes = new Map();
    let links = res.records.map(r => {
      let source = r.get("source");
      let target = r.get("target");
      let sourceRole = r.get("sourceRole") || "Unknown";
      let targetRole = r.get("targetRole") || "Unknown";
      let sourceWebsite = r.get("sourceWebsite") || null;
      let targetWebsite = r.get("targetWebsite") || null;

      nodes.set(source, { name: source, role: sourceRole, website: sourceWebsite, x: Math.random() * 500, y: Math.random() * 500 });
      nodes.set(target, { name: target, role: targetRole, website: targetWebsite, x: Math.random() * 500, y: Math.random() * 500 });
      
      return { source, target };
    });

    const updatedData = { nodes: Array.from(nodes.values()), links };
    localStorage.setItem('graphData', JSON.stringify(updatedData));
    this.setState({ data: updatedData });
  };

  componentDidMount() {
    this.loadData();
  }

  handleChange = (event) => {
    this.setState({ query: event.target.value });
  };

  render() {
    return (
      <Router>
        <div>
          <Routes>
            <Route path="/NFC" element={<NFCTrigger addNode={this.addNodeNFC} graphRef={this.graphRef} />} />
            <Route path="/" element={<GraphView data={this.state.data} query={this.state.query} handleChange={this.handleChange} loadData={this.loadData} graphRef={this.graphRef} />} />
          </Routes>
        </div>
      </Router>
    );
  }
}

const NFCTrigger = ({ addNode, graphRef }) => {
  const location = useLocation();

  React.useEffect(() => {
    const addAndRedirect = async () => {
      const newUser = `User-${Date.now()}`;
      
      try {
        await addNode(newUser);
      } catch (error) {
        console.error("Error adding user:", error);
        return;
      }

      setTimeout(() => {
        window.location.assign("/#/");
      }, 1000);
      
      setTimeout(() => {
        if (graphRef.current) {
          const node = graphRef.current.graphData().nodes.find(n => n.name === newUser);
          if (node) {
            graphRef.current.centerAt(node.x, node.y, 1000);
          }
        }
      }, 2000);
    };

    addAndRedirect();
  }, [location]);

  return <div style={{ textAlign: "center", padding: "20px", fontSize: "16px", color: "red" }}>Processing NFC tap...</div>;
};

const GraphView = ({ data, query, handleChange, loadData, graphRef }) => (
  <div width="95%">
    <textarea
      style={{ display: "block", width: "95%", height: "100px", margin: "0 auto", textAlign: "center" }}
      value={query}
      onChange={handleChange}
    />
    <button id="simulate" onClick={loadData}>Simulate</button>
    <button id="visualize" onClick={() => window.open("https://awuchen.github.io/craft-network-3d/", "_blank")}>Visualize3D</button>
    <button id="form" onClick={() => window.open("https://hako.soooul.xyz/apply/", "_blank")}>Onboard</button>
    <button id="reset" onClick={() => { window.location.reload(); }}>Reset</button>
    <ForceGraph2D
      ref={graphRef}
      graphData={data}
      nodeId="name"
      nodeLabel="name"
      nodeCanvasObject={(node, ctx) => {
        ctx.fillStyle = "white";
        ctx.strokeStyle = "black";
        ctx.lineWidth = 2;
        ctx.beginPath();
        ctx.arc(node.x || Math.random() * 500, node.y || Math.random() * 500, 6, 0, 2 * Math.PI);
        ctx.fill();
        ctx.fillStyle = "black";
        ctx.fillText(node.name, node.x + 10, node.y);
      }}
      onNodeClick={(node) => {
        if (node.website) {
          window.open(node.website, '_blank');
        }
      }}
      linkDirectionalArrowRelPos={1}
      linkDirectionalArrowLength={5}
    />
  </div>
);

export default CypherViz;